import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import { ORIGINAL_SVG_updownicon } from "../../../../utils/defaults";
import { useQuery } from "react-query";
import PersonalizedImageModal from "../PersonalizedImageModal/PersonalizedImageModal"
import generatesvgUrl from "../../../../utils/generatesvgUrl";
import { template_service } from "../../../../services/templateService";
import TemplatePreview from "./TemplatePreview";
import SearchBar from "../../../../layouts/LoggedInLayout/CreateCardLayout/components/DesignTool/Components/SubNavBar/components/ElementSelector/components/SearchBar/SearchBar";
import SelectSocialMediaTemplateModal from "../SelectSocialMediaTemplateModal/SelectSocialMediaTemplateModal";
import PersonalizedCardSizeModal from "../PersonalizedCardSizeModal/PersonalizedCardSizeModal"
const image1 = require("./../../../../assets/images/01.png");
const image2 = require("./../../../../assets/images/02.png");
const image3 = require("./../../../../assets/images/03.png");
const image4 = require("./../../../../assets/images/04.png");
interface BioProps {
    bio1: string;
    bio2: string;
    bio3: string;
    bio4: string;
}
const CreateEmail: React.FC<BioProps> = ({
    bio1,
    bio2,
    bio3,
    bio4,
}): JSX.Element => {
    const svg1 = generatesvgUrl(ORIGINAL_SVG_updownicon);
    const [DisplayModals, setDisplayModals] = useState<"" | "PersonalizedImage" | "SocialMedia" | "CardSize">("")
    const svg = generatesvgUrl(bio4);
    const { data, error, isLoading } = useQuery<any, Error>(
        "templates",
        template_service.getAllTemplates
    );

    if (isLoading) {
        return (
            <>
                <p>Getting all templates ...</p>
            </>
        );
    }

    if (!!error) {
        return (
            <>
                <p>{error.message}</p>
            </>
        );
    }

    const handleclosemodal = () => {
        setDisplayModals("")
    }
    const HandleChangeDisplayModal = (value: "PersonalizedImage" | "SocialMedia" | "CardSize") => {
        setDisplayModals(value)
    }
    return (
        <div>
            <div className="flex flex-col justify-center w-full mx-auto mt-4 ">
                <div style={{ width: "319px", height: "190px" }} className="flex flex-col px-2 py-4 mb-6 border-2 border-opacity-25 border-dashed rounded-lg border-gray95 ">
                    <div className="flex flex-row justify-center mt-3  ">
                        <img src={svg} className="mr-2" />

                        <h1 className="text-base text-indigo">{bio1}</h1>
                    </div>
                    <div className="flex justify-center mt-2 ">
                        <p className="flex justify-center leading-4 text-center text-gray95">
                            {bio2}
                        </p>
                    </div>
                    <NavLink to="/createcard"></NavLink>
                    <button onClick={() => HandleChangeDisplayModal("PersonalizedImage")} className="flex justify-center">
                        <div style={{ width: "277px", height: "38px", }} className="flex  justify-center p-2 mt-4 border-0 rounded-lg bg-indigo ">
                            <svg
                                className="mt-1 mr-4"
                                width="14"
                                height="14"
                                viewBox="0 0 14 14"
                                fill="none"
                                xmlns="http://www.w3.org/2000/svg"
                            >
                                <path
                                    d="M7 2V12M12 7L2 7"
                                    stroke="#EEF2FF"
                                    stroke-width="4"
                                    stroke-linecap="round"
                                    stroke-linejoin="round"
                                />
                            </svg>

                            <p className="text-white font-inter text-sm leading-5 font-medium not-italic ">{bio3}</p>
                        </div>
                    </button>
                </div>
            </div>
            <div style={{ display: DisplayModals === "PersonalizedImage" ? "" : "none" }}>
                <PersonalizedImageModal
                    displayModalChange={HandleChangeDisplayModal}
                    closeModal={handleclosemodal}
                />
            </div>

            <div style={{
                display: DisplayModals === "SocialMedia" ? "" : "none",
            }}>
                <SelectSocialMediaTemplateModal
                    closeModal={handleclosemodal}
                    displayModalChange={HandleChangeDisplayModal}
                />
            </div>



            <div style={{ display: DisplayModals === "CardSize" ? "" : "none", }}>
                <PersonalizedCardSizeModal
                    displayModalChange={HandleChangeDisplayModal}
                    closeModal={handleclosemodal}
                />
            </div>


        </div>
    );
};

export default CreateEmail;
